import React, { useEffect, useState } from 'react';
import { Grid, Card } from 'semantic-ui-react';

import { useSubstrate } from './substrate-lib';

function Main (props) {
  const { api } = useSubstrate();
  const { finalized } = props;
  const [blockNumber, setBlockNumber] = useState(0);
  const [blockNumberTimer, setBlockNumberTimer] = useState(0);
  const [blockHash, setBlockHash] = useState(0);


  const bestNumber = finalized
    ? api.derive.chain.bestNumberFinalized
    : api.derive.chain.bestNumber;

  useEffect(() => {
    let unsubscribeAll = null;
  let lastNumber = 0;
    bestNumber(number => {
      lastNumber = number.toNumber();
      setBlockNumber(lastNumber);
      getHashdata(lastNumber);
      setBlockNumberTimer(0);
    })
      .then(unsub => {
        unsubscribeAll = unsub;
      })
      .catch(console.error);
      
    const getHashdata = async (number) => {
      try {
        api.rpc.chain.getBlockHash(number).then( hash => {
          setBlockHash(hash.toHex());
        })
      } catch (e) {
        console.error(e);
      }
    };
    
    return () => unsubscribeAll && unsubscribeAll();
  }, [bestNumber]);

  const timer = () => {
    setBlockNumberTimer(time => time + 1);
  };

  useEffect(() => {
    const id = setInterval(timer, 1000);
    return () => clearInterval(id);
  }, []);
  
  
  return (
    <Grid.Column>
      <Card fluid>
        <Card.Content>
          <Card.Header>Last block number: {blockNumber}</Card.Header>
          <Card.Description>Last block hash: {blockHash}</Card.Description>
        </Card.Content>
      </Card>
    </Grid.Column>
  );

}
  
export default function BlockNumber (props) {
  const { api } = useSubstrate();
  return api.derive &&
    api.derive.chain &&
    api.derive.chain.bestNumber &&
    api.derive.chain.bestNumberFinalized ? (
      <Main {...props} />
    ) : null;
}
